body {
    margin: 0 !important;
}

ul {
    padding: 0;
    /* border-top: 1px solid #ccc; */
}

li {
    list-style: none;
    display: inline-block;
    min-width: 60px;
    margin: 2px;
    padding: 5px;
    cursor: pointer;
    white-space: nowrap;
    border-radius: 10%;
    -webkit-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear;
}

.explorer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 100%;
}

.symbols {
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 10px;
    margin: 5px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
    max-height: 100%;
    overflow: scroll;
}

.Circle {
    width: 100%;
}

.score {
    width: 100%;
    text-align: center;
    position: relative;
}

h5 {
    margin: 0;
}

ul {
    margin: 5px 0;
}

li:hover {
    background: #d7ffc1;
}

li.active {
    background: #58ad3e;
}

li.sub {
    background: #d7ffc1;
}

li.super {
    background: #ccc;
}

li.parallel {
    border: 1px solid #58ad3e;
}

canvas {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
}


